import React from 'react';
import {Container, Header, Content, Footer, View, Text, Button, Input } from 'native-base';
{/*App developer: 
Mamaun Abdullah
[THE TRADE CODER]

Built with: react-native, expo-cli and native-base using nodejs
*/}

export default class App extends React.Component {
  constructor(props){
    super(props);
    this.state={
      result:0,
      input:0,
      valuePerChange:1,
      reset:0
    }
    this.increment=this.increment.bind(this);
    this.decrement=this.decrement.bind(this);
    this.reset=this.reset.bind(this);
    this.handleChange=this.handleChange.bind(this);

  }
  increment(){
    this.setState((state)=>{
      return{result:state.result+state.valuePerChange}
    })
  };
  decrement(){
    this.setState((state)=>{
      return {result:state.result-state.valuePerChange}      
    })
  };
  reset(){
    this.setState({
      result:0,
      input:0
       
    })
  };
 
  handleChange(e){
    this.setState({
      valuePerChange: parseInt(e.target.value)
    })
  };
  render(){
  return (
    <Container style={ss.root}>
      <Header style={ss.header}>
        <Text style={ss.headerText}>Simple Counter</Text>
      </Header>

      <Content>
        <View style={ss.displayResult}>
          <Text style={ss.resultText}>{this.state.result}</Text>
        </View>
        
        <View style={ss.flexRowCenter}>
        <Button onPress={this.decrement} style={ss.operationButton}>
            <Text style={ss.buttonText}>-</Text>
          </Button>
          <Button onPress={this.increment} style={ss.operationButton}>
            <Text style={ss.buttonText}>+</Text>
          </Button>        
        </View>
        
        <View>
          <Button onPress={this.reset} style={ss.resetButton}>
            <Text style={ss.buttonText}>Reset</Text>
          </Button>
        </View>

        <View style={ss.inputContainer}>
          <Text style={{padding:5}}>Set unit per change:</Text>
          <Input placeholder="1" onChange={this.handleChange} style={ss.counterInput}/>
        </View>
      </Content>

      <Footer padder style={ss.footer}>
        <Text style={ss.footerText}>Dev: <br/>Mamun Abdullah <br/>THE TRADE CODER</Text>
      </Footer>
    </Container>
  );
 }
};

